.gif {
    width: 40vw;
    border-radius: 15px;

}


.box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    box-shadow: rgba(68, 72, 237, 0.3) 0px 1px 2px 0px, rgba(68, 72, 237, 0.15) 0px 1px 3px 1px;
    border-radius: 15px;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-weight: bold;
}

.form {
    width: 35vw;
    padding: 2%;
}

.formHead {
    margin: 5% 3%;
    padding: 2%;
    letter-spacing: 1px;
    color: #5e89f8;
}

.formHead div:nth-of-type(1) {
    font-size: 2rem;
    font-weight: light;

}

.formHead div:nth-of-type(2) {
    font-size: 0.85rem;
}

.formBody {

    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 3%;
}

.formBody input {
    margin: 5%;

    padding: 3% 5%;
    font-size: 0.75rem;
    box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
    border: none;
    border-radius: 10px;
    transition: 0.5s ease-in-out;
}

.formBody input::placeholder {
    font-family: 'Rubik', sans-serif;
    letter-spacing: 1px;
    color: rgba(0, 0, 0, 0.345);
}

.formBody .btns {
    margin: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formBody .btns button {
    margin: 2%;
    padding: 2.5% 4%;
    font-size: 0.8rem;
    font-family: 'Rubik', sans-serif;
    border: none;
    background-color: #5e89f8;
    color: white;
    border-radius: 5px;
    width: 80%;
    transition: 0.5s ease-in-out;
    box-sizing: border-box;
}

.error-handler{
    width: 100%;
    text-align: center;
    color: rgba(255, 0, 0, 0.738);
    font-size: 0.85rem;
}

.formBody .btns button:hover {
    background-color: white;
    color: black;
}

.formBody input:hover {
    transform: translateY(-15%);
}


@media screen and (max-width:700px) {
    .gif {
        display: none;
    }

    .box {
        box-shadow: rgba(67, 86, 232, 0.4) 5px 5px, rgba(67, 86, 232, 0.3) 10px 10px, rgba(67, 86, 232, 0.2) 15px 15px, rgba(67, 86, 232, 0.1) 20px 20px, rgba(67, 86, 232, 0.05) 25px 25px;
    }

    .form {
        padding: 3% 7%;
        width: 60vw;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        background-color: rgba(67, 86, 232, 0.516);
        border-radius: 15px;
        color: white;
    }

    .formHead {
        color: white;
    }

 
    .formBody .btns button {
        background-color: whitesmoke;
        color: black;
    }

    .formBody label {
        color: white;
    }

    .formBody .btns button:hover {
        background-color: #190e8e51;
        color: white;
    }

    .error-handler{
        color: white;
    }

}

@media screen and (max-width:550px) {
    .form {
        width: 90vw;
    }
}